import { FieldValues, Resolver } from 'react-hook-form';

import { Schema } from './schema';
import { CharacterType, ValidationErrors } from './schema/schema.types';

export const hintId = <T>(id: keyof T) => {
  return `${id as string}-hint`;
};

export const errorId = <T>(id: keyof T) => {
  return `${id as string}-error`;
};

export const capitalise = (str: string) =>
  str.charAt(0).toUpperCase() + str.slice(1);

const characterTypeMap = {
  letters: new RegExp('[A-Za-z]', 'g'),
  digits: new RegExp('[0-9]', 'g'),
};

export const count = (value: string, type?: CharacterType) => {
  if (!type) {
    return value?.length;
  }

  const regExp = characterTypeMap[type];
  const results = value.match(regExp) || [];

  return results.length;
};

export const parseString = (value?: unknown) => {
  if (typeof value === 'string') {
    const trimmed = value.replace(/\s{2,}/g, ' ').trim();
    return trimmed;
  }

  return value || '';
};

export const getAllErrors = <T extends FieldValues>(
  schema: Schema<T>,
  values: T,
) => {
  return schema.fields.reduce((acc, fieldKey) => {
    const fieldErrors =
      schema.validate(fieldKey, values[fieldKey], values) || [];

    const hasErrors = fieldErrors.length > 0;

    if (hasErrors) {
      return { ...acc, [fieldKey]: fieldErrors };
    }

    return acc;
  }, {} as ValidationErrors<T>);
};

export const resolver = <T extends FieldValues>(
  schema: Schema<T>,
): Resolver<T> => {
  return (values) => {
    const formErrors = getAllErrors(schema, values);

    return { values, errors: formErrors };
  };
};
