import React from 'react';

import { errorId, hintId } from '#utils/formValidation';

import WarningSVG from '#assets/icons/warning-solid-24.svg';

import {
  ErrorWrapper,
  ErrorMessage,
  ErrorIcon,
  HelpText,
  InputWrapper,
  Label,
  Wrapper,
  RequiredMarker,
} from './Input.styles';
import { InputState, FormInputProps } from './Input.types';

const FormInputWrapper: React.FC<FormInputProps<HTMLInputElement>> = ({
  id,
  label,
  helpText,
  className,
  message,
  state,
  required,
  children,
}) => {
  return (
    <Wrapper className={className}>
      <Label htmlFor={id}>{label}</Label>
      {required && <RequiredMarker aria-hidden={true}>*</RequiredMarker>}
      {helpText && <HelpText id={hintId(id)}>{helpText}</HelpText>}
      <InputWrapper $state={state}>{children}</InputWrapper>
      <ErrorWrapper aria-live="assertive">
        {state === InputState.ERROR && message && (
          <ErrorMessage>
            <ErrorIcon svg={WarningSVG} size="small" />
            <span id={errorId(id)}>{message}</span>
          </ErrorMessage>
        )}
      </ErrorWrapper>
    </Wrapper>
  );
};

export default FormInputWrapper;
